import React from "react";
import { ThemeProvider } from "@material-ui/core/styles";
import { Route, useLocation } from "react-router-dom";

import Header from "./UI/header/header";
import theme from "./UI/theme";
import Footer from "./UI/footer";

import Home from "./Home/home";
import AdvancedSearch from "./AdvancedSearch/AdvancedSearch/advancedSearch";
import AboutMovie from "./AdvancedSearch/AboutMovie";
import News from "./News/news";
import EditNews from "./Admin/Edit/EditNews/EditNews";
import NewNews from "./Admin/New/NewNews";
import NewMovie from "./Admin/New/NewMovie/NewMovie";
import EditMovie from "./Admin/Edit/EditMovie/EditMovie";
import NewPerson from "./Admin/New/NewPerson";
import InstitucionalContent from "./InstitucionalContent/InstitucionalContent";

import { SecureRoute, Security, LoginCallback } from "@okta/okta-react";
import { OktaAuth, toRelativeUrl } from "@okta/okta-auth-js";
import { useHistory } from "react-router-dom";
import AdminHome from "./Admin/AdminHome";
import SignInForm from "./Auth/SignInForm";

import Login from "./Auth/SignIn";
import { oktaAuthConfig, oktaSignInConfig } from "./Auth/config";

const oktaAuth = new OktaAuth(oktaAuthConfig);

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

const OktaError = ({ error }) => {
  // this is a copy/paste from this library's ./OktaError.js
  console.log(error);
  if (error.name && error.message) {
    return (
      <p>
        {error.name}: {error.message}
      </p>
    );
  }
  return <p>Error: {error.toString()}</p>;
};

function App() {
  let query = useQuery();
  const history = useHistory();

  const restoreOriginalUri = async (_oktaAuth, originalUri) => {
    history.replace(toRelativeUrl(originalUri, window.location.origin));
  };

  const customAuthHandler = () => {
    history.push("/login");
  };

  //console.log(process.env.REACT_APP_MY_ENV_VAR);
  //console.log(process.env);

  return (
    <Security
      oktaAuth={oktaAuth}
      restoreOriginalUri={restoreOriginalUri}
      onAuthRequired={customAuthHandler}
    >
      <ThemeProvider theme={theme}>
        <Header />

        <Route exact path="/" component={Home}></Route>

        <Route path="/News/:id" component={News}></Route>
        <Route path="/News/" component={News}></Route>
        <Route path="/Edit/News/:id" component={EditNews}></Route>
        <Route path="/New/News/:id" component={NewNews}></Route>

        <Route path="/New/Movie/" component={NewMovie}></Route>
        <Route path="/Edit/Movie/" component={EditMovie}></Route>

        <Route
          path="/Institucional/:id"
          component={InstitucionalContent}
        ></Route>

        <Route path="/New/People/" component={NewPerson}></Route>

        <Route
          path="/Aboutmovie"
          movieCode={query.get("movieCode")}
          component={AboutMovie}
        />
        <Route path="/login" component={SignInForm} />
        <SecureRoute path="/Admin" component={AdminHome} />
        <Route
          path="/callback"
          component={() => <LoginCallback errorComponent={<OktaError />} />}
        />

        <Route
          path="/busquedaavancada/:pathParam1?/:pathParam2?"
          component={AdvancedSearch}
        ></Route>

        <Footer />
      </ThemeProvider>
    </Security>
  );
}

export default App;
